import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { LoadingController } from '@ionic/angular';
import { ReplaySubject } from 'rxjs';
import { CachingService } from 'src/app/services/caching.service';
import { FavoritesService } from 'src/app/services/favorites.service';
import { PageService } from 'src/app/services/page.service';
import { StorageService } from 'src/app/services/storage.service';
import { VimeoService } from 'src/app/services/vimeo.service';

@Component({
  selector: 'app-training-program-content',
  templateUrl: './training-program-content.component.html',
  styleUrls: ['./training-program-content.component.scss'],
})
export class TrainingProgramContentComponent implements OnInit {
  sections: any;
  asyncSections: ReplaySubject<any[]> = new ReplaySubject<any[]>();
  pageId: any;
  pageExcerpt: any;
  pageInfo: any;
  latestTrainingPrograms: any = [];
  asyncLatestTrainingPrograms: ReplaySubject<any[]> = new ReplaySubject<any[]>();
  loading: any;
  isLoading: boolean;

  constructor(
    private pageService: PageService,
    private activeRouter: ActivatedRoute,
    public favoritesService: FavoritesService,
    public loadingCtrl: LoadingController,
    public cachingService: CachingService,
    private storageService: StorageService,
    private vimeoService: VimeoService
  ) { }

  ngOnInit() {
    this.activeRouter.params.subscribe(async (res) => {
      this.startLoading();
      const trainingId = 'trainingSections' + res.id;
      this.pageId = res.id;

      this.pageService.getSinglePage(res.id).subscribe((res: any) => {
        try {
          this.pageExcerpt = this.pageService.parsePureExcerpt(res.pure_excerpt);
        } catch(e) {
          this.pageExcerpt = {};
        }
        
        this.pageInfo = {
          clean_title: res.clean_title,
          pure_excerpt: res.pure_excerpt,
          id: res.id
        };
        try {
          this.pageInfo.excerpt = this.pageService.parsePureExcerpt(this.pageInfo.pure_excerpt);
        } catch(e) {}
        window.localStorage.setItem('page' + this.pageId, JSON.stringify(this.pageInfo));
      });

      this.pageService.getPageByParent(res.id, 1, 50).subscribe(async (res: any) => {
        if(res[0]) {
          if (this.cachingService.notChangedSince(res, trainingId + 'date')) {
            this.loadFromCache();
            this.stopLoading();
            return ;
          }
        }

        res = res.sort((a,b) => {
          return a.title.rendered > b.title.rendered ? 1 : -1;
        });
        
        this.sections = [];

        for(let item of res) {
          const temp: any = {};
          try {
            temp.excerpt = this.pageService.parsePureExcerpt(item.pure_excerpt)
          } catch(e) {
            // Must not be the case at all
            temp.excerpt = {};
          }

          temp.content = item.content.rendered;
          this.sections.push(temp);
        }

        this.asyncSections.next(this.sections);
        this.stopLoading();
        
        setTimeout(async () => await this.stopLoading(), 800);
        window.localStorage.setItem(trainingId, JSON.stringify(this.sections))
      }, err => {
        this.stopLoading();
        this.loadFromCache();
      })
      const cacheLatest = await this.storageService.get('latestTrainingPrograms');
      this.latestTrainingPrograms = cacheLatest ? JSON.parse(cacheLatest) : [];
      this.asyncLatestTrainingPrograms.next(this.latestTrainingPrograms);
    });
  }

  addFavoritePage() {
    this.favoritesService.addFavoritePage(this.pageId, {
      pageType: 'training-programs',
      title: this.pageInfo.clean_title,
      ...this.pageExcerpt
    }).subscribe(res => {
      console.log(res);
    });
  }

  removeFavoritePage() {
    this.favoritesService.removeFavoritePage(this.pageId).subscribe(res => {
      console.log(res);
    });
  }

  async loadFromCache() {
    this.startLoading();
    const trainingId = 'trainingSections' + this.pageId;

    if(window.localStorage.getItem(trainingId)) {
      this.sections = JSON.parse(window.localStorage.getItem(trainingId));
    }

    if (this.sections.length) {
      this.asyncSections.next(this.sections);
    }

    if(window.localStorage.getItem('page' + this.pageId)) {
      this.pageInfo = JSON.parse(window.localStorage.getItem('page' + this.pageId));
    }
    
    
    this.latestTrainingPrograms = window.localStorage.getItem('latestTrainingPrograms');
    this.latestTrainingPrograms = this.latestTrainingPrograms ? JSON.parse(this.latestTrainingPrograms) : [];

    if (!this.latestTrainingPrograms || !this.latestTrainingPrograms.length) {
      const cacheLatest = await this.storageService.get('latestTrainingPrograms');
      this.latestTrainingPrograms = cacheLatest ? JSON.parse(cacheLatest) : [];
    }

    if(this.sections) {
      setTimeout(async () => await this.stopLoading(), 800);
    }
  }

  async startLoading() {
    if(this.loading) {
      // Just in case loading is still opened
      this.loading.dismiss();
    }
    this.loading = await this.loadingCtrl.create();

    this.loading.present();
    this.isLoading = true;
  }

  async stopLoading() {
    if(!this.loading) {
      return ;
    }
    this.loading.dismiss();
    this.isLoading = false;
  }

  listAllContent(): void{
    this.vimeoService.listAllContent();
  }

}
